body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
}

.landing-page {
  height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black; /* Default text color is black */
}

.landing-page::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('background.jpg') no-repeat center center;
  background-size: cover;
  opacity: 0.3; /* Set opacity to make the background image lighter */
  z-index: -1; /* Ensure the overlay appears behind the content */
}

.navbar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1; /* Ensure navbar stays above the background */
}

.cloud-shape {
  background-color: white;
  border-radius: 50px;
  padding: 10px 20px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.cloud-shape h1 {
  margin: 0;
  font-size: 1.5em;
  color: black; /* Set the heading text color to black */
}

.navbar ul {
  display: flex;
  list-style-type: none;
}

.navbar ul li {
  margin: 0 15px;
}

.navbar ul li a {
  text-decoration: none;
  color: black; /* Set navbar link text to black */
  font-weight: bold;
}

.signup-btn {
  background-color: #fff;
  color: #ff7e5f;
  padding: 10px 20px;
  border-radius: 20px;
}

.content {
  text-align: center;
  z-index: 1; /* Ensure the content stays above the background */
}

.content h1 {
  font-size: 3em;
  margin-bottom: 20px;
  color: red; /* Set the heading color to red */
}

.content p {
  font-size: 1.2em;
  margin-bottom: 30px;
  color: black; /* Set the paragraph color to black */
}

.buttons .btn {
  padding: 15px 30px;
  border-radius: 30px;
  text-decoration: none;
  font-size: 1em;
  font-weight: bold;
  margin: 0 10px;
}

.buttons .btn.primary {
  background-color: #fff;
  color: #ff7e5f;
}

.buttons .btn.secondary {
  background-color: transparent;
  border: 2px solid #fff;
  color: #fff;
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: white;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 300px; /* Set width of modal */
  border-radius: 10px; /* Rounded corners */
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.modal-content h2 {
  margin: 0 0 15px;
  text-align: center;
}

.modal-content form {
  display: flex;
  flex-direction: column;
}

.modal-content input {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.modal-content button {
  background-color: #007BFF; /* Primary button color */
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.modal-content button:hover {
  background-color: #0056b3; /* Darker shade on hover */
}

